import { Component, input, model, output } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ButtonModule } from 'primeng/button';
import { StepperModule } from 'primeng/stepper';
import { Step } from '../../models';

@Component({
  selector: 'app-stepper',
  imports: [CommonModule, ButtonModule, StepperModule],
  templateUrl: './stepper.component.html',
  styleUrls: ['./stepper.component.scss'],
})
export class StepperComponent {
  readonly steps = input<Step[]>([]);
  readonly currentStepValue = model<number>(1);
  readonly showNavButtons = input(true);
  readonly linearProgression = input(false);
  readonly containerClass = input('basis-[50rem]');
  readonly showSteps = input(true);

  readonly stepChange = output<number>();
  readonly complete = output<void>();
  readonly nextStep = output<number>();
  readonly prevStep = output<number>();
  readonly loading = input(false);

  isLastStep(value: number): boolean {
    return value === Math.max(...this.steps().map((s) => s.value));
  }

  isFirstStep(value: number): boolean {
    return value === Math.min(...this.steps().map((s) => s.value));
  }

  getNextStep(currentValue: number): number {
    const sortedValues = this.steps()
      .map((s) => s.value)
      .sort((a, b) => a - b);
    const currentIndex = sortedValues.indexOf(currentValue);
    return sortedValues[currentIndex + 1] || currentValue;
  }

  getPrevStep(currentValue: number): number {
    const sortedValues = this.steps()
      .map((s) => s.value)
      .sort((a, b) => a - b);
    const currentIndex = sortedValues.indexOf(currentValue);
    return sortedValues[currentIndex - 1] || currentValue;
  }

  // Methods for external navigation
  goNext(): void {
    const nextStep = this.getNextStep(this.currentStepValue());
    if (nextStep !== this.currentStepValue()) {
      this.currentStepValue.set(nextStep);
      this.stepChange.emit(nextStep);
      this.nextStep.emit(nextStep);
    }
  }

  goBack(): void {
    const prevStep = this.getPrevStep(this.currentStepValue());
    if (prevStep !== this.currentStepValue()) {
      this.currentStepValue.set(prevStep);
      this.stepChange.emit(prevStep);
      this.prevStep.emit(prevStep);
    }
  }

  // Check if we can finish the wizard
  canFinish(): boolean {
    return (
      this.isLastStep(this.currentStepValue()) &&
      !(this.getCurrentStep()?.disableNext ?? false)
    );
  }

  // Get the current step object
  getCurrentStep(): Step | undefined {
    return this.steps().find((s) => s.value === this.currentStepValue());
  }
}
